.fontStyle {
  font-size: 20px;
  font-weight: 500;
}
.forgetPwdStyle {
  text-align: center;
  padding: 10px 0;
  border-bottom: 1px solid #ccc;
}
.forgetPwdStyle .headerContent img {
  width: 100px;
  height: 100px;
}
.forgetPwdStyle .middleContent {
  padding-top: 100px;
}
.forgetPwdStyle .middleContent .leftContentHeader {
  font-size: 20px;
  font-weight: 500;
}
.forgetPwdStyle .middleContent .leftContent {
  width: 50%;
  margin: 0 auto;
  padding-top: 5%;
  letter-spacing: 2px;
  font-size: 17px;
}
.forgetPwdStyle .middleContent .rightContent .rightContent-1 {
  font-size: 20px;
  font-weight: 500;
}
.forgetPwdStyle .middleContent .rightContent .formStyle {
  width: 40%;
  margin: 0 auto;
}
.forgetPwdStyle .middleContent .rightContent .formStyle .inputCode {
  widows: 50%;
  float: left;
}
.forgetPwdStyle .middleContent .rightContent .formStyle button {
  background-color: #1f78d1;
  border-color: #1b69b6;
  color: #fff;
}
.forgetPwdStyle .middleContent .rightContent .formStyle .hasVerifyCode {
  width: 100%;
}
.forgetPwdStyle .middleContent .rightContent .formStyle .countDownCode {
  color: white;
  font-weight: 700;
  cursor: pointer;
}
.forgetPwdStyle .middleContent .rightContent .formStyle .sendEmailButton_2 {
  clear: left;
}
.forgetPwdStyle .middleContent .rightContent .formStyle .sendEmailButton_2 .sendEmail_2 {
  width: 100%;
}
