._logo {
  text-align: center;
}
._logo :global .ant-layout .logo {
  background: 'transparent';
  height: initial;
}
._logo span {
  display: inline-block;
  line-height: 32px;
  font-weight: bold;
  width: 100%;
  font-size: 23px;
}
._logo span .logo-Image {
  width: 50px;
  height: 50px;
  display: inline-block;
  padding: 3%;
}
._logo span .logo-Content {
  font-size: 20px;
  color: white;
  vertical-align: sub;
  letter-spacing: 1px;
}
.siderItem {
  padding: 20px 0;
}
